
'use server';
/**
 * @fileOverview A speech-to-text transcription AI agent.
 *
 * - transcribeAudio - A function that handles audio transcription.
 * - TranscribeAudioInput - The input type for the transcribeAudio function.
 * - TranscribeAudioOutput - The return type for the transcribeAudio function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';
import {TranscriptWord} from '@/lib/types';

const TranscribeAudioInputSchema = z.object({
  audioDataUri: z
    .string()
    .describe(
      "A recording of spoken audio, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  language: z.string().describe("The language of the audio to be transcribed. e.g., 'English', 'Khmer'"),
});
export type TranscribeAudioInput = z.infer<typeof TranscribeAudioInputSchema>;

const TranscriptWordSchema = z.object({
  text: z.string(),
  start: z.number().describe('Start time of the word in seconds.'),
  end: z.number().describe('End time of the word in seconds.'),
});

const TranscribeAudioOutputSchema = z.object({
  transcript: z.array(TranscriptWordSchema).describe('The structured transcript with word timings.'),
  text: z.string().describe('The full transcribed text as a single string.'),
});
export type TranscribeAudioOutput = z.infer<typeof TranscribeAudioOutputSchema>;


export async function transcribeAudio(input: TranscribeAudioInput): Promise<TranscribeAudioOutput> {
  return transcribeAudioFlow(input);
}

const prompt = ai.definePrompt({
  name: 'transcribeAudioPrompt',
  input: {schema: TranscribeAudioInputSchema},
  output: {schema: TranscribeAudioOutputSchema},
  prompt: `You are a highly accurate audio transcription service.
Transcribe the following audio in {{language}}. Ensure the transcription is as accurate as possible.
Include punctuation and structure the output as a clean, readable text.
Provide the full text and a structured transcript with precise word-level timestamps.

Audio: {{media url=audioDataUri}}`,
  model: 'googleai/gemini-2.0-flash',
});

const transcribeAudioFlow = ai.defineFlow(
  {
    name: 'transcribeAudioFlow',
    inputSchema: TranscribeAudioInputSchema,
    outputSchema: TranscribeAudioOutputSchema,
  },
  async (input) => {
    const {output} = await prompt(input);
    if (!output) {
      throw new Error('Transcription failed: The model did not return any output.');
    }
    
    // Post-process to ensure start and end times are numbers.
    const processedTranscript = output.transcript.map(word => ({
        ...word,
        start: Number(word.start) || 0,
        end: Number(word.end) || 0,
    }));

    return {
        transcript: processedTranscript,
        text: output.text,
    };
  }
);
