
"use client";

import { useState, useEffect, useRef } from 'react';
import { Download, Upload, Settings, Loader2, Mic } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { useToast } from '@/hooks/use-toast';
import { exportTranscript } from '@/lib/export';
import type { TranscriptWord } from '@/lib/types';
import { transcribeAudio } from '@/ai/flows/speech-to-text-flow';
import { EditorView } from './editor-view';
import { TranscriptView } from './transcript-view';
import { cn } from '@/lib/utils';
import { Label } from './ui/label';
import { Input } from './ui/input';
import { Card } from './ui/card';

const blobToBase64 = (blob: Blob): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      if (typeof reader.result === 'string') {
        resolve(reader.result);
      } else {
        reject(new Error('Failed to convert blob to Base64.'));
      }
    };
    reader.onerror = reject;
    reader.readAsDataURL(blob);
  });
};


export function VoiceScribePage() {
  const [audioFile, setAudioFile] = useState<File | null>(null);
  const [audioUrl, setAudioUrl] = useState<string | null>(null);
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [structuredTranscript, setStructuredTranscript] = useState<TranscriptWord[]>([]);
  const [editedTranscript, setEditedTranscript] = useState('');
  const [wordsPerSecond, setWordsPerSecond] = useState<number | undefined>(undefined);
  const [language, setLanguage] = useState('English');

  const [exportFormat, setExportFormat] = useState('srt');
  const [isDragging, setIsDragging] = useState(false);

  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    const processAudio = async () => {
        if (!audioFile) return;
        
        const newAudioUrl = URL.createObjectURL(audioFile);
        setAudioUrl(newAudioUrl);

        setIsTranscribing(true);
        setStructuredTranscript([]);
        setEditedTranscript('');

        try {
            const audioDataUri = await blobToBase64(audioFile);
            const result = await transcribeAudio({ audioDataUri, language });
            
            if (result && result.transcript) {
                setStructuredTranscript(result.transcript);
                setEditedTranscript(result.text);
            } else {
                toast({
                    title: "Transcription failed",
                    description: "The model did not return a transcript. Please try again.",
                    variant: "destructive",
                });
            }
        } catch (e: any) {
            console.error(e);
            toast({
                title: "Transcription Error",
                description: e.message || "An error occurred while processing your audio.",
                variant: "destructive",
            });
        } finally {
            setIsTranscribing(false);
        }
    };
    processAudio();

    return () => {
        if (audioUrl) {
            URL.revokeObjectURL(audioUrl);
        }
    }
  }, [audioFile, toast, language]);

  const handleFileSelect = (file: File | null | undefined) => {
    if (file && file.type.startsWith('audio/')) {
        setAudioFile(file);
    } else if (file) {
        toast({
            title: "Invalid file type",
            description: "Please select an audio file.",
            variant: "destructive",
        });
    }
  }

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    handleFileSelect(event.target.files?.[0]);
  };

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleDragEvents = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    if (e.dataTransfer.items && e.dataTransfer.items.length > 0) {
        setIsDragging(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
  };
  
  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
    handleFileSelect(e.dataTransfer.files?.[0]);
  };

  const handleExport = () => {
    exportTranscript(editedTranscript, exportFormat as 'srt' | 'txt' | 'vtt', structuredTranscript, toast, wordsPerSecond);
  };

  const isReadyForContent = !isTranscribing && structuredTranscript.length > 0;

  return (
    <div 
        className="flex flex-col h-screen bg-muted/40"
        onDragEnter={handleDragEnter}
        onDragOver={handleDragEvents}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
    >
        <header className="flex flex-wrap items-center justify-between gap-4 p-4 border-b bg-background shadow-sm">
            <div className="flex items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold tracking-tight">VoiceScribe</h1>
                    <p className="text-muted-foreground">Upload, edit, and export your audio with ease.</p>
                </div>
            </div>
            <div className="flex items-center gap-2">
                 <input
                    type="file"
                    ref={fileInputRef}
                    onChange={handleFileChange}
                    className="hidden"
                    accept="audio/mp3,audio/wav,audio/ogg,audio/m4a,audio/*"
                />

                <Select value={language} onValueChange={setLanguage} disabled={isTranscribing}>
                  <SelectTrigger className="w-[120px]">
                      <SelectValue placeholder="Language" />
                  </SelectTrigger>
                  <SelectContent>
                      <SelectItem value="English">English</SelectItem>
                      <SelectItem value="Khmer">Khmer</SelectItem>
                  </SelectContent>
                </Select>

                <Button 
                    onClick={handleUploadClick} 
                    disabled={isTranscribing}
                >
                    {isTranscribing ? <Loader2 className="animate-spin" /> : <Upload />}
                    <span className="ml-2 hidden sm:inline">{isTranscribing ? 'Transcribing...' : 'Select File'}</span>
                </Button>

                <div className="flex items-center gap-1 rounded-md border bg-background">
                  <Select value={exportFormat} onValueChange={setExportFormat} disabled={!isReadyForContent}>
                      <SelectTrigger className="w-[100px] border-0 shadow-none focus:ring-0">
                          <SelectValue placeholder="Format" />
                      </SelectTrigger>
                      <SelectContent>
                          <SelectItem value="srt">SRT</SelectItem>
                          <SelectItem value="txt">TXT</SelectItem>
                          <SelectItem value="vtt">VTT</SelectItem>
                      </SelectContent>
                  </Select>
                  <Button 
                      onClick={handleExport}
                      variant="ghost"
                      className="border-l rounded-none"
                      disabled={!isReadyForContent}
                  >
                      <Download />
                      <span className="ml-2 hidden sm:inline">Export</span>
                  </Button>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                       <Button variant="ghost" size="icon" className="border-l rounded-none">
                        <Settings />
                       </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent className="w-64" align="end">
                        <DropdownMenuLabel>Export Settings</DropdownMenuLabel>
                        <DropdownMenuSeparator />
                        <DropdownMenuItem onSelect={(e) => e.preventDefault()}>
                          <div className="grid gap-2">
                            <Label htmlFor="words-per-second">Words per second</Label>
                            <Input 
                                id="words-per-second"
                                type="number"
                                placeholder="e.g. 1"
                                value={wordsPerSecond ?? ''}
                                onChange={(e) => setWordsPerSecond(e.target.value ? Number(e.target.value) : undefined)}
                                className="bg-background"
                            />
                            <p className="text-xs text-muted-foreground">For SRT/VTT. Overrides AI timing.</p>
                          </div>
                        </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </div>
            </div>
        </header>

        <main className="flex-grow p-4 md:p-6 grid grid-cols-1 md:grid-cols-2 gap-6 relative">
            {isTranscribing ? (
                <div className="absolute inset-0 bg-background/80 flex flex-col items-center justify-center z-10">
                    <Loader2 className="h-12 w-12 animate-spin text-primary" />
                    <p className="text-muted-foreground mt-4 text-lg">Transcribing audio, please wait...</p>
                </div>
            ) : null}

            {!audioFile ? (
                <div 
                    className={cn(
                        "md:col-span-2 flex flex-col items-center justify-center text-center flex-grow rounded-lg border-2 border-dashed border-muted-foreground/20 bg-background h-full transition-colors",
                        isDragging && "border-primary bg-primary/10"
                    )}
                >
                    <Mic className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                    <h3 className="text-2xl font-semibold">Ready to Transcribe</h3>
                    <p className="text-muted-foreground mt-2 mb-4">Drop an audio file here or click the upload button.</p>
                    <Button onClick={handleUploadClick} variant="outline" size="lg">
                        <Upload className="mr-2"/>
                        Upload Audio
                    </Button>
                </div>
            ) : (
                <>
                    <Card className="flex flex-col h-full shadow-sm">
                        <EditorView
                            transcript={editedTranscript}
                            onTranscriptChange={setEditedTranscript}
                            disabled={!isReadyForContent}
                        />
                    </Card>
                    <Card className="flex flex-col h-full shadow-sm">
                        <TranscriptView
                            structuredTranscript={structuredTranscript}
                            audioUrl={audioUrl}
                        />
                    </Card>
                </>
            )}
        </main>
    </div>
  );
}
