
"use client";

import { useState, useRef, useEffect } from 'react';
import type { TranscriptWord } from '@/lib/types';
import { Play, Pause } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Slider } from '@/components/ui/slider';

interface TranscriptViewProps {
  structuredTranscript: TranscriptWord[];
  audioUrl: string | null;
}

export function TranscriptView({ structuredTranscript, audioUrl }: TranscriptViewProps) {
  const [isPlaying, setIsPlaying] = useState(false);
  const [canPlay, setCanPlay] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const activeWordRef = useRef<HTMLSpanElement | null>(null);

  useEffect(() => {
    if (audioUrl) {
      const audio = new Audio(audioUrl);
      audioRef.current = audio;

      const handleCanPlay = () => {
        setCanPlay(true);
        setDuration(audio.duration);
      };

      const handleTimeUpdate = () => setCurrentTime(audio.currentTime);
      const handlePlay = () => setIsPlaying(true);
      const handlePause = () => setIsPlaying(false);
      const handleEnded = () => setIsPlaying(false);
      
      audio.addEventListener('canplay', handleCanPlay);
      audio.addEventListener('timeupdate', handleTimeUpdate);
      audio.addEventListener('play', handlePlay);
      audio.addEventListener('pause', handlePause);
      audio.addEventListener('ended', handleEnded);

      audio.load();

      return () => {
        audio.removeEventListener('canplay', handleCanPlay);
        audio.removeEventListener('timeupdate', handleTimeUpdate);
        audio.removeEventListener('play', handlePlay);
        audio.removeEventListener('pause', handlePause);
        audio.removeEventListener('ended', handleEnded);
        audio.pause();
        audio.src = '';
      };
    }
  }, [audioUrl]);

  useEffect(() => {
    if (activeWordRef.current) {
      activeWordRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
      });
    }
  }, [currentTime]);

  const handlePlayPause = () => {
    if (audioRef.current && canPlay) {
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        audioRef.current.play().catch(e => console.error("Playback error:", e));
      }
    }
  };

  const handleSeek = (value: number[]) => {
    if(audioRef.current && canPlay) {
      audioRef.current.currentTime = value[0];
      setCurrentTime(value[0]);
    }
  }

  return (
    <div className="flex h-full flex-col">
      <ScrollArea className="flex-grow w-full">
        <div className="p-6">
            <p className="text-base leading-relaxed">
            {structuredTranscript.map((word, index) => {
                const isActive = currentTime >= word.start && currentTime < word.end;
                return (
                <span
                    key={index}
                    ref={isActive ? activeWordRef : null}
                    className={`transition-colors duration-150 ${
                    isActive ? 'bg-primary/20 rounded-md' : ''
                    }`}
                >
                    {word.text}{' '}
                </span>
                );
            })}
            </p>
        </div>
      </ScrollArea>

      <div className="flex items-center gap-4 border-t p-4">
        <Button onClick={handlePlayPause} size="icon" variant="ghost" disabled={!audioUrl || !canPlay}>
          {isPlaying ? <Pause className="h-6 w-6" /> : <Play className="h-6 w-6" />}
        </Button>
        <Slider
          value={[currentTime]}
          max={duration}
          step={0.1}
          onValueChange={handleSeek}
          disabled={!audioUrl || !canPlay}
        />
      </div>
    </div>
  );
}
